body {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  font-family: "FiraSans", "Arial", sans-serif;
  color: #484848; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.container {
