  flex-direction: column;
  margin-top: 32%;
  margin-bottom: 15%; }

.main-block__title {
  display: flex;
  margin-bottom: 32px;
  width: 350px;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }

.main-block__mouse-border {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  border: 1px dashed #ffffff;
  border-radius: 50%;
