  border-radius: 50%; }

.main-block__img::after {
  position: absolute;
  content: "";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 17px;
  height: 26px;
  background-image: url("../img/css_sprites.png");
  background-position: -226px -10px; }

@media (max-width: 1023px) {
  .main-block {
    margin-top: 22%;
    margin-bottom: 30%; } }

@media (max-width: 767px) {
  .main-block {
    margin-top: 83%;
    margin-bottom: 47%; }
  .main-block__title {
    width: 320px;
    font-size: 20px;
    line-height: 24px; } }

.main__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 49px;
  text-align: center;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1fc0fc; }

@media (max-width: 767px) {
  .main__title {
    font-size: 32px;
    line-height: 39px; } }

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../img/bg-watercolor-about.png");
  background-position: bottom;
  background-repeat: no-repeat;
  padding-bottom: 96px;
  margin-bottom: 104px; }

.about__title {
  margin: 0;
  margin-bottom: 24px; }

.about__text {
  width: 720px;
  font-size: 17px;
  line-height: 170%;
  text-align: center;
  margin-bottom: 48px; }

.about__player {
  position: relative; }

.about__play-btn-border {
  cursor: pointer;
  position: absolute;
  width: 96px;
  height: 96px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 16px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%; }

.about__play-btn {
  width: 64px;
  height: 64px;
  background-color: #ffffff;
  border-radius: 50%; }

.about__play-btn-img {
  position: absolute;
  left: 54%;
  top: 50%;
  transform: translate(-50%, -50%); }

@media (max-width: 767px) {
  .about {
    padding-bottom: 0;
    background-image: url("../img/bg-watercolor-about-mobile.png");
    background-size: 100%;
    background-position: center;
    margin-bottom: 80px; }
  .about__title {
    margin-bottom: 24px; }
  .about__text {
    font-size: 16px;
    line-height: 160%;
    width: 100%; }
  .about__play-btn-border {
    transform: scale(0.84) translate(-50%, -50%); } }

.programs {
  background-image: url("../img/bg-sketch-program.png");
  background-size: 680px auto;
  background-repeat: no-repeat;
  background-position: right 0; }

.programs__title {
  text-align: left;
  padding-top: 48px;
  margin-bottom: 83px; }

.programs__info-slider {
  display: flex; }

.programs__nav {
  display: flex;
  flex-direction: column;
  width: 230px; }

.programs__label {
  position: relative;
  font-size: 15px;
  line-height: 18px;
  opacity: 0.87;
  padding-left: 72px;
  padding-right: 11px;
  padding-top: 25px;
  padding-bottom: 25px;
