.programs__label[for="program-1"]::before,
.programs__label[for="program-2"]::before,
.programs__label[for="program-3"]::before,
.programs__label[for="program-4"]::before,
.programs__label[for="program-5"]::before {
  position: absolute;
  content: "";
  left: 20px;
  top: 50%;
  transform: translateY(-50%); }

.programs__label[for="program-1"]::before {
  width: 28px;
  height: 33px;
