  background: url("../img/css_sprites.png") -174px -100px; }

.programs__btn--internships:checked ~ .programs__info-slider .programs__label[for="program-3"]::before {
  background: url("../img/css_sprites.png") -174px -55px; }

.programs__label[for="program-4"]::before {
  width: 32px;
  height: 29px;
  background: url("../img/css_sprites.png") -122px -59px; }

.programs__btn--volunteering:checked ~ .programs__info-slider .programs__label[for="program-4"]::before {
  background: url("../img/css_sprites.png") -122px -10px; }

.programs__label[for="program-5"]::before {
  width: 23px;
  height: 25px;
  background: url("../img/css_sprites.png") -96px -173px; }

.programs__btn--religious:checked ~ .programs__info-slider .programs__label[for="program-5"]::before {
  background: url("../img/css_sprites.png") -53px -173px; }

.programs__btn--general:checked ~ .programs__info-slider .programs__label[for="program-1"],
.programs__btn--academic:checked ~ .programs__info-slider .programs__label[for="program-2"],
.programs__btn--internships:checked ~ .programs__info-slider .programs__label[for="program-3"],
.programs__btn--volunteering:checked ~ .programs__info-slider .programs__label[for="program-4"],
.programs__btn--religious:checked ~ .programs__info-slider .programs__label[for="program-5"] {
  background: linear-gradient(108.42deg, #ffc341 20.46%, #ffd701 65.31%);
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 500;
  opacity: 1; }

.programs__btn--general:checked ~ .programs__info-slider .programs__label[for="program-1"]::after,
.programs__btn--academic:checked ~ .programs__info-slider .programs__label[for="program-2"]::after,
.programs__btn--internships:checked ~ .programs__info-slider .programs__label[for="program-3"]::after,
.programs__btn--volunteering:checked ~ .programs__info-slider .programs__label[for="program-4"]::after,
.programs__btn--religious:checked ~ .programs__info-slider .programs__label[for="program-5"]::after {
  position: absolute;
